body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, #795ec2, #eaafc8);
}




@font-face {
    font-family: 'Amberg Regular';
    font-style: normal;
    font-weight: normal;
    src: local('Amberg Regular'), url(/robofriends/static/media/Amberg.6882e5f4.woff) format('woff');
}

h1 {
    font-family: 'Amberg Regular';
    font-weight: 500;
    color: black;
}
